<template>
  <div>

    <navheader></navheader>
    <div class="list-content">
      <p v-for="(listdex,index) in list2">
        <a href="#" @click="btn2(index)">
          <img :src="listdex.img" alt="">
          <span>{{listdex.name}}</span>
        </a>
      </p>

    </div>

    <navfooter></navfooter>


  </div>
</template>

<script>
  import Navheader from './components/Navheader.vue';
  import Navfooter from './components/footer.vue';
  export default {
    data() {
      return {
        list2:[{name:'1-V',img:'./static/img/list16.jpg',price:4550},
          {name:'2-VS',img:'./static/img/list17.jpg',price:7250},
          {name:'3-X',img:'./static/img/list18.jpg',price:8400},
          {name:'4-XX',img:'./static/img/list19.jpg',price:8650},
          {name:'4-7F',img:'./static/img/list20.jpg',price:7400}],
        imgs:['./static/img/list3-1.jpg','./static/img/list3-2.jpg','./static/img/list3-3.jpg']
      }
    },
    methods:{
      btn2: function (index) {
        this.$router.push('/shop1?aa='+JSON.stringify(this.list2[index])+'&bb='+JSON.stringify(this.imgs));
      }
    },
    components:{
      Navheader,
      Navfooter
    }
  };
</script>



