<template>
 <div class="pic1">
<swiper :options="swiperOption" ref="mySwiperA" class="pic2">

    <swiper-slide v-for="item in pictures" :key='item.id'>
      <img :src="item" alt="">
    </swiper-slide>


  <!-- <div class="swiper-pagination" slot="pagination"></div>
  <div class="swiper-button-prev" slot="button-prev"></div>
  <div class="swiper-button-next" slot="button-next"></div> -->
</swiper>



</div>
</template>


<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";
import "./../assets/css/swiper.css";

export default {
  props:['pictures'],
  components: {
    swiper,
    swiperSlide
  },
  data() {
    return {
//      pics: [
//        { img: "././static/img/list1-1.jpg" },
//        { img: "./../static/img/list1-2.jpg" },
//        { img: "./../static/img/list1-3.jpg" }
//      ],//pics
      swiperOption: {
        // Swiper
        notNextTick: false,
        autoplay: 1000,
        grabCursor: true,
        setWrapperSize: true,
        autoHeight: true,
        loop: true,
        pagination: ".swiper-pagination",
        paginationClickable: true,
        prevButton: ".swiper-button-prev",
        nextButton: ".swiper-button-next",
        mousewheelControl: true,
        observeParents: true,
        onTransitionStart(swiper) {
          console.log(swiper);
        }
      }
    };
  },
  computed: {
    swiper() {
      return this.$refs.mySwiperA.swiper;
    }
  },
  mounted() {
//    console.log("");
    this.swiper.slideTo(2, 1000, true);
  }
}

</script>



