<template>
  <div>
    <navheader></navheader>
    <auto-play :pictures="imgs"></auto-play>
    <div class="content container-fluid" id="shop">
      <div class="row">
        <div class="col-xs-12 content-shop">
          <h3>{{shop.name}}</h3>
          <p><span>:</span>{{shop.price}}</p>
          <p><span></span>
            <select>
              <option></option>
              <option></option>
              <option></option>
            </select>
          </p>
          <p><a href="javascript:"></a></p>
          <p><button @click="back"></button></p>
        </div>
        <div class="col-xs-12">
          <img src="./../static/img/list1--1.jpg" alt="">
        </div>
        <div class="col-xs-12">
          <img src="./../static/img/list1--2.jpg" alt="">
        </div>
        <div class="col-xs-12">
          <img src="./../static/img/list1--3.jpg" alt="">
        </div>
        <div class="col-xs-12">
          <img src="./../static/img/list1--4.jpg" alt="">
        </div>
      </div>
    </div>
    <navfooter></navfooter>
  </div>
</template>


<script>
  import Navheader from './components/Navheader.vue';
  import AutoPlay from './components/autoplay.vue';
  import Navfooter from './components/footer.vue';

  export default {
//    props:['imgs'],
    data() {
      return {
        shop: {name: "3", price: '4000'},
        imgs:''
//        imgs:['./../static/img/list1-1.jpg','./../static/img/list1-2.jpg','./../static/img/list1-3.jpg']

      }
    },
    methods:{
    back(){
      this.$router.push('/')
    }
    },
    mounted:function () {
      this.shop=JSON.parse(this.$route.query.aa);
      this.imgs=JSON.parse(this.$route.query.bb);
       window.scrollTo(0,0);
    },
    components: {
      Navheader,
      Navfooter,
      AutoPlay
    }
  }
</script>



