<template>
  <div>

    <navheader></navheader>
    <div class="list-content">
      <p v-for="(listdex,index) in list2">
        <a href="#" @click="btn2(index)">
          <img :src="listdex.img" alt="">
          <span>{{listdex.name}}</span>
        </a>
      </p>

    </div>

  <navfooter></navfooter>


  </div>
</template>

<script>
  import Navheader from './components/Navheader.vue';
  import Navfooter from './components/footer.vue';
  export default {
    data() {
      return {
        list2:[{name:'-V',img:'./static/img/list26.jpg',price:6000},
          {name:'-VS',img:'./static/img/list27.jpg',price:7000},
          {name:'-X',img:'./static/img/list28.jpg',price:8000},
          {name:'-XX',img:'./static/img/list29.jpg',price:4900},
          {name:'-7F',img:'./static/img/list30.jpg',price:7200}],
        imgs:['./static/img/list2-1.jpg','./static/img/list2-2.jpg','./static/img/list2-3.jpg']
      }

    },
    methods:{
      btn2: function (index) {
        this.$router.push('/shop1?aa='+JSON.stringify(this.list2[index])+'&bb='+JSON.stringify(this.imgs));
      }
    },
    components:{
      Navheader,
      Navfooter
    }
  };
</script>



