<template>
  <div class="content container-fluid">
    <div class="row">
      <div class="col-xs-6">
        <img src="./../assets/img/content1.png" alt="" @click="golist1">
        <img src="./../assets/img/content1-1.png" alt="">
        <p></p>
      </div>
      <div class="col-xs-6">
        <img src="./../assets/img/content2.png" alt="" @click="golist2">
        <img src="./../assets/img/content2-1.png" alt="">
        <p></p>
      </div>
      <div class="col-xs-6">
        <img src="./../assets/img/content3.png" alt="" @click="golist1">
        <img src="./../assets/img/content3-1.png" alt="">
        <p></p>
      </div>
      <div class="col-xs-6">
        <img src="./../assets/img/content4.png" alt="" @click="golist2">
        <img src="./../assets/img/content4-1.png" alt="">
        <p></p>
      </div>
      <div class="col-xs-12">
        <img src="./../assets/img/main1.jpg" alt="">
      </div>
      <div class="col-xs-12">
        <img src="./../assets/img/main2.jpg" alt="">
      </div>
    </div>

  </div>
</template>
<script>
  export default {
    methods:{
      golist1(){
        this.$router.push('/list1')
      },
      golist2(){
        this.$router.push('/list2')
      }
    }
  }
</script>



