<template>
<div>
  <navheader></navheader>
<div class="list-content">
    <p v-for="(listdex,index) in list1">
        <a href="javascript:" @click="btn1(index)">
            <img :src="listdex.img" alt="">
            <span>{{listdex.name}}</span>
        </a>
    </p>
</div>


  <navfooter></navfooter>

</div>

</template>

<script>
  import Navheader from './components/Navheader.vue';
  import Navfooter from './components/footer.vue';
  let router1 = 'shop1';
export default {
  data() {
    return {
      list1: [
        { name: "3", img: "./static/img/list21.jpg", price: 5000 },
        { name: "4", img: "./static/img/list22.jpg", price: 6000 },
        { name: "5", img: "./static/img/list23.jpg", price: 7000 },
        { name: "6", img: "./static/img/list24.jpg", price: 5500 },
        { name: "7", img: "./static/img/list25.jpg", price: 4200 }
      ],
      imgs:['./static/img/list1-1.jpg','./static/img/list1-2.jpg','./static/img/list1-3.jpg']
    }
  },
  methods:{
    btn1: function (index) {
      this.$router.push('/shop1?aa='+JSON.stringify(this.list1[index])+'&bb='+JSON.stringify(this.imgs));
    }
  },
  components:{
    Navheader,
    Navfooter
  }
};
</script>



